.profile {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-top: 40px;
    padding-bottom: 51px;
}

@media screen and (max-width: 720px) {
    .profile {
      padding-top: 42px;
      padding-bottom: 36px;
      flex-direction: column;
    }
  }
